.site-layout-content {
  background: #fff;
  padding: 24px;
  min-height: 280px;
}
.logo {
  width: 120px;
  height: 31px;
  background: rgba(125, 255, 255, 0.2);
  margin: 16px 24px 16px 0;
  float: left;
}
[data-theme="dark"] .site-layout-content {
  background: #141414;
}
[data-theme="dark"] .logo {
  background: rgba(125, 255, 255, 0.3);
}
